*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  min-height: 100vh;
  background-color: #262626;
  justify-content: center;
  align-items: center;
  font-family: Orbitron, sans-serif;
  display: flex;
  position: relative;
}

.container {
  width: min(90%, 30rem);
  grid-template-columns: repeat(3, 1fr);
  gap: .8rem;
  display: grid;
}

.box {
  width: 10rem;
  height: 10rem;
  position: relative;
}

.box:after {
  content: "";
  height: 100%;
  width: 100%;
  background: radial-gradient(60rem at 0 0, #fff9, #0000 20%);
  background: radial-gradient(60rem circle at var(--mouse-x) var(--mouse-y), #fff9, transparent 20%);
  z-index: 1;
  transition: opacity .5s;
  position: absolute;
}

.box-content {
  z-index: 2;
  color: #fff;
  background-color: #262626;
  justify-content: center;
  align-items: center;
  font-size: 1.4rem;
  display: flex;
  position: absolute;
  inset: .3rem;
}

.box:hover {
  background-color: #fff6;
}

.creator {
  color: #fff;
  align-items: center;
  gap: 2rem;
  display: flex;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
}

.creator__heading {
  font-size: 1.8rem;
  font-weight: 500;
}

.creator__socials {
  align-items: center;
  gap: 2rem;
  display: flex;
}

.creator__social svg {
  width: 3rem;
  height: 3rem;
  fill: #fff;
}

.logo-box {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
}

.logo-box img {
  width: 5rem;
  height: 5rem;
}

